import socketService from '../Services/SocketService.js'

export default {
    state: {
        msgs: []
    },
    getters: {
        msgs(state) {
            return state.msgs;
        }
    },
    mutations: {
        addMsg(state, { msg }) {
            state.msgs.push(msg);
        }
    },
    actions: {
        chatJoin(context, { trip, user }) {
            socketService.emit('chatJoin', { trip, user })
            socketService.on('chat newMsg', msg => context.commit({ type: 'addMsg', msg }));
        },
        sendMsg(context, { trip,user, msg }) {
            socketService.emit('chat msg', {trip,user, msg})
        },

    }
}