<template>
  <section class="trip-chat">
    <div class="group-chat">
      <h3>new chat</h3>
      <!-- <ul>
        <li v-for="(msg, i) in trip.chat.msgs" :key="i">{{msg.from}}: {{msg.txt}}</li>
      </ul> -->
      <ul  v-if="loggedInUser">
        <li v-for="(msg, i) in msgs" :key="i">{{msg.from}}: {{msg.txt}}</li>
      </ul>
    </div>

    <div class="new-msg-area flex">
      <input type="text" class="new-msg-input" v-model="msg" />
      <button @click="sendMsg">Send</button>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    trip: Object
  },
  data() {
    return {
      msg: ""
    };
  },
  created() {
    this.$store.dispatch({
      type: "chatJoin",
      trip: this.trip,
      user: this.loggedInUser
    });
  },
  computed: {
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    },
    msgs() {
      return this.$store.getters.msgs;
    }
  },
  methods: {
    sendMsg() {
      this.$store.dispatch({
        type: "sendMsg",
        trip: this.trip,
        user: this.loggedInUser,
        msg: this.msg
      });
    }
  }
};
</script>


<style lang="scss" scoped>
@import "../styles/components/_TripChat";
</style>

