<template>
  <section class="nav" app :class="{ colorBg: colorBg }">
    <div class="navbar">
      <div class="logo">
        <img src="../assets/img/vamos-logo.png" alt />
      </div>
      <div class="nav-links">
        <button class="btn-link" v-if="loggedInUser" @click="logout">Logout</button>
        <router-link v-if="!loggedInUser" to="/Signup">Signup</router-link>
        <button class="btn-link" v-if="!loggedInUser" @click="toggleLogin">Login</button>
        <router-link to="/about">About</router-link>
        <router-link to="/">Home</router-link>
        <img v-if="loggedInUser" @click="goUserProfile" class="user-img" :src="loggedInUser.imgUrl" />
      </div>
    </div>
  </section>
</template>



<script>
export default {
  data() {
    return {};
  },
  created() {},
  computed: {
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    }
  },
  methods: {
    logout() {
      this.$store.dispatch({ type: "logout" });
    },
    goUserProfile() {
      this.$router.push(`/UserProfile/${this.loggedInUser._id}`);
    },
    toggleLogin() {
      this.$store.commit("toggleLogin");
    }
  },
  props: {
    colorBg: {
      type: Boolean,
      default: false
    }
  }
};
</script>



<style lang="scss" scoped>
@import "../styles/components/_Header.scss";
</style>
