<template>
  <section class="home">
    <div class="header">
      <AppHeader />
      <Login v-if="loginModal"/>
      <TripFilter class="filter" @setFilter="setFilter" />
      <router-link to="/TripEdit">New Trip</router-link>
    </div>

    <div v-if="ownerTrips.length && loggedInUser" class="my-trips">
      <h2>My Trips</h2>
      <TripList :trips="ownerTrips" />
    </div>
    <div class="all-trips">
      <TripList :trips="trips" />
    </div>
  </section>
</template>


<script>
import TripList from "../components/TripList";
import TripFilter from "../components/TripFilter";
import AppHeader from "../components/Header";
import Login from "../components/Login";

export default {
  data() {
    return {
      ownerTrips: []
    };
  },
  created() {
    this.setFilter();
  },
  computed: {
    trips() {
      return this.$store.getters.trips;
    },
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    },
    loginModal(){
      return this.$store.getters.loginModal;
    }
  },
  methods: {
    async setFilter(filter = {}) {
      const tripsToShow = await this.$store.dispatch({
        type: "loadTrips",
        filter
      });
      if (this.loggedInUser)
        this.ownerTrips = tripsToShow.filter(
          trip => trip.owner._id === this.loggedInUser._id
        );
    }
  },
  components: {
    TripList,
    TripFilter,
    AppHeader,
    Login
  }
};
</script>


<style lang="scss" scoped>
@import "../styles/views/_Home.scss";
</style>