<template>
  <section class="trip-edit">
    <AppHeader class="colorBg"/>
    <Login v-if="loginModal" />
    <div class="edit-form-title">
      <h2 v-if="trip._id">Edit</h2>
      <h2 v-else>New Trip</h2>
      <form @submit.prevent="save">
       
        
          <label>
            Title
            <div><input type="text" v-model="trip.title" placeholder="Title" required autofocus/></div>
          </label>
        
         <label>
          Destination
        </label>
         <span><GoogleAutocomplete @placeSelected="placeSelected($event)" required></GoogleAutocomplete></span>
        
        
          <label>
            Start Date
            <div><input type="date" class="short-input" v-model="trip.start" required/></div>
          </label>
        
        
          <label>
            End Date
            <div><input type="date" class="short-input" v-model="trip.end" required/></div>
          </label>
        
        <div class="checkBox-container">
          <!-- <v-container fluid> -->
          <div>
            <v-checkbox v-model="trip.types" label="Day trip" value="Day trip"></v-checkbox>
            <v-checkbox v-model="trip.types" label="Culture" value="Culture"></v-checkbox>
          </div>
          <div>
            <v-checkbox v-model="trip.types" label="Historical" value="Historical"></v-checkbox>
            <v-checkbox v-model="trip.types" label="Guided Tour" value="Guided Tour"></v-checkbox>
          </div>
          <div>
            <v-checkbox v-model="trip.types" label="Cycling" value="Cycling"></v-checkbox>
            <v-checkbox v-model="trip.types" label="Motorcycle Tour" value="Motorcycle Tour"></v-checkbox>
          </div>
          <div>
            <v-checkbox v-model="trip.types" label="Camping" value="Camping"></v-checkbox>
            <v-checkbox v-model="trip.types" label="Hiking" value="Hiking"></v-checkbox>
          </div>
          <div>
            <v-checkbox v-model="trip.types" label="Beach" value="Beach"></v-checkbox>
            <v-checkbox v-model="trip.types" label="Markets" value="Markets"></v-checkbox>
          </div>
          <div>
            <v-checkbox v-model="trip.types" label="Culinary" value="Culinary"></v-checkbox>
            <v-checkbox v-model="trip.types" label="Spiritual" value="Spiritual"></v-checkbox>
          </div>
          <!-- </v-container> -->
        </div>
        
          <label>
            Description
            <div><textarea v-model="trip.desc" placeholder="Description" cols="30" rows="10"></textarea></div>
          </label>
        
        
          <label>
            Meeting Point
            <div><input type="text" v-model="trip.meetingPoint" placeholder="Meeting Point" /></div>
          </label>
        
        
          <label>
            Meeting Time
            <div><input type="time" class="input-number" v-model="trip.meetingTime" placeholder="Meeting Time" /></div>
          </label>
        
        
          <label>
            Max Participants
            <div><input type="number" class="input-number" v-model="trip.participantCap" min="2" /></div>
          </label>
        
        
          <label>
            Upload Image
            <div><input type="file" class="short-input" ref="upload" /></div>
          </label>
        
        <button :disabled="requireds">Save</button>
      </form>
    </div>
  </section>
</template>


<script>
import AppHeader from "../components/Header";
import GoogleAutocomplete from "../components/GoogleAutocomplete";
import Login from "../components/Login";
export default {
  data() {
    return {
      trip: {
        title: "",
        desc: "",
        destination: null,
        imgUrl:
          "https://images.unsplash.com/photo-1562386130-926081dc4fbf?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=600&q=60",
        types: [],
        start: "",
        end: "",
        likedBy: [],
        participantCap: "",
        meetingTime: "",
        meetingPoint: "",
        chat: { msgs: [] }
      }
    };
  },
  async created() {
    const tripId = this.$route.params.id;
    if (tripId) {
      this.trip = await this.$store.dispatch({ type: "getById", tripId });
      console.log(this.trip);
    }
  },
  computed: {
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    },
    loginModal() {
      return this.$store.getters.loginModal;
    },
  },
  methods: {
    async save() {
      const file = this.$refs.upload.files[0];
      // loading.io
      if (file) {
        try {
          const url = await this.$store.dispatch({ type: "uploadImg", file });
          this.trip.imgUrl = url;
        } catch (err) {
          console.log(err);
          // swal
        }
      }
      try {
        const newTrip = await this.$store.dispatch({
          type: "save",
          trip: this.trip
        });
        this.loggedInUser.tripsOwner.push({
          tripId: newTrip._id,
          waitingList: []
        });
      } catch (err) {
        console.log(err);
      }
      try {
        await this.$store.dispatch({
          type: "updateUser",
          user: this.loggedInUser
        });
      } catch (err) {
        console.log(err);
      }
      this.$router.push("/");
    },
    placeSelected(e) {
      // this.destination = e;
      this.trip.destination = e.administrative_area_level_1;
    }
  },
  components: {
    AppHeader,
    GoogleAutocomplete,
    Login
  }
};
</script>


<style lang="scss" scoped>
@import "../styles/views/_TripEdit.scss";
</style>
