<template>
  <section v-if="trip && owner" class="trip-details">
    <div class="AppHeader">
      <AppHeader class="colorBg" />
    </div>
    <Login v-if="loginModal" />
    <div class="trip-desc">
      <div class="flex space-between">
        <div class="flex column">
          <h2>{{trip.destination}}, {{trip.title}}</h2>
          <h4>{{trip.start}} - {{trip.end}}</h4>
          <h4>{{trip.participantCap}}</h4>
          <h4>{{trip.desc}}</h4>
          <ul class="flex">
            <li class="type" v-for="(type, i) in trip.types" :key="i">{{type}}</li>
          </ul>
        </div>
        <i
          class="material-icons"
          v-if="loggedInUser && loggedInUser._id === trip.owner._id"
          @click="goEdit"
        >edit</i>
        <i
          class="material-icons"
          v-if="loggedInUser && loggedInUser._id === trip.owner._id"
          @click="remove(trip)"
        >delete_outline</i>
        <button
          class="btn-status"
          :disabled="ownerEqualLoggegInUser"
          :value="status"
          @click="joinTrip"
        >{{status}}</button>
      </div>
      <div class="owner-details flex">
        <img class="user-img-desc" :src="owner.imgUrl" />
        <h3>Organizer - {{owner.firstName}} {{owner.lastName}}</h3>
      </div>
    </div>
    <div class="map">
      <google-map :location="trip.destination" />
    </div>
    <!-- <div class="trip-plan"></div> -->
    <div class="trip-weather">
      <h3>Wether</h3>
      <Weather :location="trip.destination" />
      <Weather :location="trip.destination" @weather="setWeather" />
      <template v-if="forecastWeather">
        <div class="render-weather">
          <div v-for="(item,i) in forecastWeather" :key="i">
            <img class="weather-img" width="70" :src="item.img" />
            <p class="weather-temp">{{item.minTemp}}</p>
            <p class="weather-temp">{{item.maxTemp}}</p>
          </div>
        </div>
      </template>
    </div>
    <div class="trip-participant">
      <button class="btn-participating" @click="toggleParticipantsModal">Participating</button>
      <button class="btn-pending" @click="showPendingModal">
        Pending
        <span class="waitingList-length" v-if="waitingList">{{waitingList.length}}</span>
      </button>
      <ul v-if="showParticipating">
        <Participants v-for="(user,i) in participantsList" :key="i" class="flex" :user="user" />
      </ul>
      <ul v-if="showPending">
        <Pending
          v-for="(user,i) in waitingList"
          :key="i"
          class="flex"
          :user="user"
          :owner="owner"
          :trip="trip"
        />
      </ul>
    </div>
    <div class="trip-chat">
      <TripChat :trip="trip" />
    </div>
  </section>
</template>

<script>
import AppHeader from "../components/Header";
import GoogleMap from "../components/GoogleMap";
import Weather from "../components/Weather";
import TripChat from "../components/TripChat";
import Login from "../components/Login";
import Participants from "../components/Participants";
import Pending from "../components/Pending";
export default {
  data() {
    return {
      trip: null,
      owner: null,
      forecastWeather: null,
      showParticipating: true,
      showPending: false
    };
  },
  async created() {
    const tripId = this.$route.params.id;
    try {
      this.trip = await this.$store.dispatch({ type: "getById", tripId });
      this.owner = await this.$store.dispatch({
        type: "getUserById",
        id: this.trip.owner._id
      });
      await this.$store.dispatch({
        type: "getUsers",
        tripId: { tripId: this.trip._id }
      });
    } catch (err) {
      console.log("in created", err);
    }
  },
  computed: {
    loginModal() {
      return this.$store.getters.loginModal;
    },
    ownerEqualLoggegInUser() {
      return this.loggedInUser && this.owner._id === this.loggedInUser._id;
    },
    loggedInUser() {
      return this.$store.getters.loggedInUser;
    },
    waitingList() {
      return this.$store.getters.waitingList;
    },
    participantsList() {
      return this.$store.getters.participantsList;
    },
    status() {
      // if(this.loggedInUser && this.owner._id === this.loggedInUser._id) return '';
      if (!this.loggedInUser) return "Login to Join!";
      const pendingRequest = this.loggedInUser.pending.find(
        id => id.tripId === this.trip._id
      );
      if (pendingRequest) return "Leave";
      else return "Join";
    }
  },
  methods: {
    goEdit() {
      this.$router.push(`/TripEdit/${this.trip._id}`);
    },
    toggleParticipantsModal() {
      this.showParticipating = !this.showParticipating;
      this.showPending = !this.showPending;
    },
    showPendingModal() {
      this.showPending = !this.showPending;
      this.showParticipating = !this.showParticipating;
    },
    async remove(trip) {
      const idx = this.owner.tripsOwner.findIndex(
        currTrip => currTrip.tripId === trip._id
      );
      console.log("idx 160 details", idx);

      try {
        await this.$store.dispatch({ type: "remove", trip });
        this.owner.tripsOwner.splice(idx, 1);
        await this.$store.dispatch({ type: "updateUser", user: this.owner });
        this.$router.push("/");
      } catch (err) {
        console.log("not remove", err);
      }
    },
    async joinTrip(ev) {
      if (!this.loggedInUser) {
        // this.$router.push("/Signup");
        return;
      }
      console.log(ev.target.value);
      const tripToUpdate = this.owner.tripsOwner.find(
        currTrip => currTrip.tripId === this.trip._id
      );
      if (ev.target.value === "Join") {
        // if(!tripToUpdate.waitingList) tripToUpdate.waitingList = [];
        tripToUpdate.waitingList.push({
          userId: this.loggedInUser._id
        });
        this.loggedInUser.pending.push({ tripId: this.trip._id });
      } else {
        const idx = tripToUpdate.waitingList.findIndex(
          id => id.userId === this.loggedInUser._id
        );
        tripToUpdate.waitingList.splice(idx, 1);
        const idxUserPending = this.loggedInUser.pending.find(
          id => id.tripId === this.trip._id
        );
        this.loggedInUser.pending.splice(idxUserPending, 1);
      }

      try {
        await this.$store.dispatch({ type: "updateUser", user: this.owner });
      } catch (err) {
        console.log("not saved", err);
      }
      try {
        await this.$store.dispatch({
          type: "updateUser",
          user: this.loggedInUser
        });
        await this.$store.dispatch({
          type: "getUsers",
          tripId: { tripId: this.trip._id }
        });
      } catch (err) {
        console.log("not update", err);
      }
    },
    setWeather(weather) {
      // console.log(weather[0].img==="../assets/img/01d.png");

      this.forecastWeather = weather;
    }
  },
  components: {
    AppHeader,
    GoogleMap,
    Weather,
    TripChat,
    Login,
    Participants,
    Pending
  }
};
</script>




<style lang="scss" scoped>
@import "../styles/views/_TripDetails.scss";
</style>
