<template>
  <section class="trip-filter">
    <form @submit.prevent="setFilter">
      <v-flex xs10 sm3>
        <v-text-field
          solo
          slot="activator"
          v-model="filter.destination"
          @input="setFilter"
          label="Destination"
        ></v-text-field>
      </v-flex>
      <v-flex xs10 sm3>
        <v-menu>
          <v-text-field
            solo
            :value="filter.start"
            slot="activator"
            label="From"
            prepend-inner-icon="date-range"
          ></v-text-field>
          <v-date-picker v-model="filter.start" no-title @input="setFilter"></v-date-picker>
        </v-menu>
      </v-flex>
      <v-flex xs10 sm3>
        <v-menu>
          <v-text-field solo :value="filter.end" slot="activator" label="Until"></v-text-field>
          <v-date-picker v-model="filter.end" no-title @input="setFilter"></v-date-picker>
        </v-menu>
      </v-flex>
      <v-flex xs10 sm4>
        <v-select
          @input="setFilter"
          v-model="filter.types"
          :items="items"
          attach
          chips
          label="Types"
          multiple
          solo
        ></v-select>
      </v-flex>
    </form>
  </section>
</template>


<script>
export default {
  data() {
    return {
      filter: {
        destination: "",
        start: null,
        end: null,
        types: []
      },
      items: [
        "Day trip",
        "Culture",
        "Historical",
        "Guided Tour",
        "Cycling",
        "Camping",
        "Hiking",
        "Beach",
        "Motorcycle Tour",
        "Markets",
        "Culinary",
        "Spiritual"
      ]
    };
  },
  created() {},
  computed: {},
  methods: {
    setFilter() {
      console.log(this.filter);
 
      this.$emit("setFilter", this.filter);
    }
  }
};
</script>


<style lang="scss" scoped>
@import "../styles/components/_TripFilter.scss";
</style>
